.blogItem {
  padding: 50px 0;

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    font-size: 48px;
    font-weight: 900;
    text-align: center;
    color: $txt_color;

    @include onTablet {
      font-size: 31px;
    }
  }

  &__data {
    text-align: center;
    color: $txt_color;
  }

  &__img {
    margin: 0 auto 20px;
    width: 100%;
  }

  &__description {
    width: 90%;
    color: $txt_color;
    margin: 0 auto;
    line-height: 1.5;
  }
}